header .vc-small-screen .searchbar.mobile {
    width: 100%;
    border-top: 1px solid #ccc;
    background: #fff;
    padding: 5px 20px;
}

header #search-form #header-search-icon {
    border-radius: 0 2px 2px 0;
    min-width: 40px;
}

body header .vc-small-screen #search-form,
body.rtl header .vc-small-screen #search-form {

    #header-search-icon {
        background-color: $main-color;
        border-radius: 2px 0 0 2px;
        min-width: 40px;

        i {
            height: 100%;
            color: #fff;

        }
    }

    input {
        border: 1px solid $main-color;
        width: calc(100% - 40px);
    }

}

@media only screen and (max-width: 1192px) {
    #main-category {
        display: flex !important;
    }

}

@media only screen and (max-width: 992px) {
    header .vc-small-screen #search-form {
        margin: 0;

        .btn-toolbar {
            margin: 0;
        }
    }
    body.rtl .velocity-divide-page .left {

    }
    #sort-by.sorter select {
        left: auto;
        right: 25px;
    }
    .category-page-wrapper .category-container .filters-container .toolbar-wrapper > div.col-4 span {
        left: auto;
        right: 5px;
    }
    .nav-container .wrapper ul li .category-logo, .nav-container .wrapper ul li .language-logo-wrapper {
        margin-right: 0;
        margin-left: 5px;
    }
    .nav-container .wrapper ul li .category-logo, .nav-container .wrapper ul li .language-logo-wrapper {
        height: auto;
        width: 25px;
    }
    .nav-container {
        font-size: 1.3rem;

        .wrapper {
            .greeting {

            }

            ul {
                li {
                    font-size: 1.2rem;
                }
            }

            .vc-customer-options {
                li {
                    font-size: 1.4rem;

                    i.icon {
                        font-size: 1.7rem;
                        display: block;
                        line-height: 1.7rem;
                    }
                }
            }

            ul.category-wrapper {
                li {
                    display: flex;
                    font-size: 1.3rem;

                    i {
                        align-self: center;
                        margin-right: auto;
                        padding-top: 0;
                    }
                }
            }
        }
    }
    .main-content-wrapper .vc-header > div.vc-small-screen .right-vc-header .badge-wrapper {
        left: -7px;
        position: relative;
        top: -24px;

        .badge {
            padding: 0.2em 0.4em;
        }
    }
    .main-content-wrapper .vc-header > div.vc-small-screen {
        .hamburger-wrapper {
            height: 50px;
            display: flex;
            align-items: center;
        }

        .login-wrapper {
            height: 50px;
            display: flex;
            align-items: center;
            a{
                align-items: center;
                border: 1px solid #e0e0e2;
                padding: 3px 6px;
                border-radius: 5px;
                line-height: normal;
            }
            i {
                transform: scaleX(-1);
            }
        }
        .search-wrapper{
            height: 50px;
            display: flex;
            align-items: center;
        }
    }
    section.product-extra-detail .extra-attributes .nav-item a.nav-link {
        font-size: 0.8rem;
    }
    #home-right-bar-container {
        top: 0;
    }
    .nav-container .wrapper .greeting {
        &> i {
            font-size: 1.6rem;
        }
        span{
            font-size: 1.6rem;
        }
    }
}

@media only screen and (max-width: 375px) {
    .selective-div {
        width: 110px;
    }
    #extra-attributesContent {
        .tab-pane {
            display: block !important;
        }
    }
}
@media only screen and (max-width: 600px) {
.main-content-wrapper .vc-header > div.vc-small-screen {
    .hamburger-wrapper {
        height: 50px;
        display: flex;
        align-items: center;
    }

    .login-wrapper {
        font-size: 1.3rem;
        a{
            padding: 3px 3px;
        }

    }
    .search-wrapper{
        height: 50px;
        display: flex;
        align-items: center;
    }
}
}